.modal{
  width: 100%;
  height: 100%;
  position: fixed;
  pointer-events: none;
  top: 0px;
  left: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: 0.5s;
}

.modal_content{
  padding: 20px 35px;
  border-radius: 20px;
  background-color: rgb(255, 255, 255);
  /* border: 1px solid black; */
  transform: scale(0.5);
  transition: 0.5s;
  box-shadow: 3px 3px 3px 5px gray;
}

.modal.active{
  opacity: 1;
  pointer-events: all;
}

.modal_content.active{
  transform: scale(1);
}

.xmark_icon {
  height: 30px;
  position: absolute;
  top: 18px;
  right: 18px;
  cursor: pointer;
}


